.App {
  text-align:start;
  background: linear-gradient(145deg, #b64040, #993636);
  box-shadow:  25px 25px 50px #4b1a1a,
               -25px -25px 50px #ff5e5e;
padding:5px;
margin-bottom:-22px;
}

.Para{
  text-align: center;
  background-color: #ffffff ;
}

.box{
  display: flex;
  border-radius: 0px;
  background: #aa3c3c;
}

.box > div {
  width: 250px;
  height: 250px;
  display: flex;
  align-items: center;
  background-color: #aa3c3c;
  border-radius: 47px;
  background: linear-gradient(145deg, #b64040, #993636);
  box-shadow:  25px 25px 50px #4b1a1a,
               -25px -25px 50px #ff5e5e;
  padding: 40px ;
  margin: 50px; 
  font: size 5px; 
}

